import React,{ Component }  from 'react';
import { Button, Card,Popover ,Result,Spin,Modal,Progress,Table} from 'antd';
import { Link} from 'react-router-dom';
import '../../App.css';
import airdrop from '../../ABI/LGairdrop';
import gettime from '../../ABI/gettime';
import web3 from "../../ABI/web3";
import LG from '../../ABI/LG-ERC20';
import airdropEveryDay from '../../ABI/airdropEveryDay';
import { SmileOutlined, FrownTwoTone, MehTwoTone, LikeTwoTone,
          QuestionCircleOutlined,CopyOutlined
        } from '@ant-design/icons';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import Invite from '../../ABI/invite';
import LGC from '../../ABI/LGC';

class lgairdrop extends Component {
  constructor(props){
    super(props);
    this.state = {
        AirdropAmount:"0",
        airdropadr:"0x46654a212dD62C86F5A839d045Ac48C5973B2681",  //LG空投合约地址
        airdropEveryDay_adr:"0x8792DBA91C4E28EF621C45d85b9AceD99D0E9CB9",  //每日空投合约地址
        AirdropAmountEveryDay:"0",
        time:"",
        message:"",
        disabled:'',
        disabled_b:'',
        user:'',  //用户帐号
        _adayamount:'',
        loadwindow:false, //加载窗
        loading:'block', //加载中
        complete:'none', //加载完成
        loadmessage:'',  //加载窗信息
        Resultwindow:'', //结果窗
        lastHB:'',  //每日一领处理后结果
        _lastHB:'', //每日一领原始结果
        chainIdDisabled:'',
        Disabled2:'',
        time_hour:'',
        time_minute:'',
        time_second:'',
        _icon:'',
        invite:'0x0000000000000000000000000000000000000001', //推荐人
        inviteMy:'', //邀请链接
        friendWindow:false, 
        friendList:{},
        friendInfoWindow:'',
        friendInfo:'',
      }
    }
    
  async componentDidMount(){
    //判断页面是否安装了Metamask
    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
    
        //初始化Provider
        const provider = window['ethereum']

        //获取网络ID
        if(provider.chainId != 0x80){
          this.setState({chainIdDisabled:"disabled"})
          this.setState({Disabled2:"disabled"})
          this.setState({message:"您当前帐户非火币链，请切换至火币链"})
        };
  
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        this.setState({user:window.defaultAccount});

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })

      }catch(e){}
    }else{console.log('没有metamask')}

    //邀请链接处理
    const invite = {} ;
    const _invite = window.location.search;
    const reg = /[?&][^?&]+=[^?&]+/g;
    const found = _invite.match(reg);
    if(found){
      found.forEach(item => {
        let temp = item.substring(1).split('=');
        let key = temp[0];
        let value = temp[1];
        invite[key] = value;
      })
    }
    if(invite.address !== undefined){
      this.setState({invite:invite.address})
    }
    console.log(this.state.invite)

    this.setState({inviteMy:'https://cn.lg-qkl.xyz/?address='+ this.state.user})
    // this.setState({inviteMy:'http://localhost:3000/?address='+ this.state.user})
    console.log(this.state.inviteMy)

    //获取空投池LG余额
    const Airdrop_LGbalance = await LG.methods.balanceOf(this.state.airdropadr).call();
    this.setState({Airdrop_LGbalance});
    
    //获取每日一领池LG余额
    const AirdropEveryDay_LGbalance = await LG.methods.balanceOf(this.state.airdropEveryDay_adr).call();
    const cc = parseFloat(web3.utils.fromWei(AirdropEveryDay_LGbalance,"ether")).toFixed(0);
    this.setState({AirdropEveryDay_LGbalance:cc});

    //获取首页LG空投单个量
    const AirdropAmount = await airdrop.methods.AirdropAmount().call();
    if(AirdropAmount/10**18 <= Airdrop_LGbalance/10**18){
    this.setState({AirdropAmount:parseFloat(web3.utils.fromWei(AirdropAmount,"ether")).toFixed(2)})}

    //供应倒计时
    const time0 = await gettime.methods.getTime().call();
    const time_hour = Math.floor(time0/3600);
    this.setState({time_hour: time_hour + '小时'});
    if(time0 < 3600 ){
      const time_minute = Math.floor(time0/60);
      this.setState({time_minute: time_minute + '分'});
    }
    if(time0 < 60 ){
      this.setState({time_second: time0 + '秒'});
    }
    //获取当日额
    const _adayamount = await airdropEveryDay.methods._adayamount().call();
    const _adayamount2 = parseFloat(web3.utils.fromWei(_adayamount,"ether")).toFixed(0);
    if(_adayamount2 > 9 ){
      this.setState({_adayamount:_adayamount2});
    }else{
      this.setState({_adayamount:0});
      this.setState({Disabled2:"disabled"}); 
      this.setState({message:"今日额度已领完，距供应--"+ this.state.time_hour 
        + this.state.time_minute + this.state.time_second});
    }

    //获取每日一领数量
    const AirdropAmountEveryDay = await airdropEveryDay.methods.AirdropAmount().call();
    this.setState({AirdropAmountEveryDay});
  
    if(_adayamount/10**18 < AirdropAmountEveryDay/10**18*2*0.9 ){
      this.setState({disabled_b:'disabled'})
    }
  
    //获取领取频率
    const time = await airdropEveryDay.methods.time().call();
    this.setState({time});

  
    }
    
    addDayamount =async () => {
      await airdropEveryDay.methods.addDayamount().send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({loadmessage:"打包中……",
          loadwindow: true,
          loading:'block',
          complete:'none'
        })
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"供应成功，可继续领取",
          loading:'none',
          complete:'block'
        })
        }
      })
    }

    getResult = async() => {
      const lastHB = this.state._lastHB;
      const _lastHB = await parseFloat(web3.utils.fromWei(lastHB,"ether")).toFixed(2);
      const bb = await parseFloat(web3.utils.fromWei(this.state.AirdropAmountEveryDay,"ether")).toFixed(2);
      this.setState({lastHB:_lastHB + 'LG'});
      const iconL = (
        < FrownTwoTone />
      );
      const iconLM = (
        < MehTwoTone />
      );
      const iconGM = (
        < SmileOutlined />
      );
      const iconG = (
        < LikeTwoTone />
      );
      if( _lastHB < (bb*2*0.15).toFixed(2)){
        this.setState({_icon:iconL})
      }
      if( _lastHB > (bb*2*0.15).toFixed(2)  && _lastHB < (bb*2*0.5).toFixed(2)){
        this.setState({_icon:iconLM})
      }
      if( _lastHB >= (bb*2*0.5).toFixed(2) && _lastHB < (bb*2*0.85).toFixed(2)){
        this.setState({_icon:iconGM})
      }
      if( _lastHB > (bb*2*0.85).toFixed(2)){
        this.setState({_icon:iconG})
      }
    }

    getAirdrop =async () => {
      await airdrop.methods.getAirdrop(this.state.invite).send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({loadmessage:"打包中……",
          loadwindow: true,
          loading:'block',
          complete:'none'
        })
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"领取成功可继续领取下面的1天1领",
          loading:'none',
          complete:'block'
        })
        }
      })
    }

    getAirdropEveryDay = async () =>{
      await airdropEveryDay.methods.getAirdrop(this.state.invite).send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({loadmessage:"打包中……",
          loadwindow: true,
          loading:'block',
          complete:'none'
        })
        }
      })
      .on('receipt',(receipt)=>{
        console.log(receipt.status)
        console.log(receipt.events[0].raw.data)
        this.setState({_lastHB:receipt.events[0].raw.data})
        if(receipt.status === true){
          this.setState({loadwindow: false})
        }
      })
      this.ResultwindowOpen();
    }

    friendInfoOpen = async()=>{
      const friendList =[]
      const friends = await Invite.methods.getFriends().call({from:window.defaultAccount});
      for(var i = 0; i < friends.length; i++){
        const lgc = await LGC.methods.balanceOf(friends[i]).call();
        const a=[];
        a[0] = friends[i];
        a[1] = lgc;
        friendList.push(a);
      }
      this.setState({friendList})
      this.setState({friendWindow: true})
    }

    //加载窗开关
    loadwindowClose = () => {
      this.setState({loadwindow: false})
    };

    //结果窗关
    ResultwindowClose = () => {
      this.setState({Resultwindow: false})
    };

    //结果窗开
    ResultwindowOpen = () => {
      this.getResult();
      this.setState({Resultwindow: true})
    };

    //好友列表窗关
    friendWindowClose = () => {
      this.setState({friendWindow: false})
    };
    //好友详情窗关
    friendInfoClose = () => {
      this.setState({friendInfoWindow: false})
    };

  render(){
    const columns = [
      {
        title: '好友帐号',
        dataIndex: '0',
        align: 'left',
        ellipsis: 'true',
      },
      {
        title: 'LGC',
        dataIndex: '1',
        width:'60px',
        align: 'right',
      },
    ];

    const content = (
      <div style={{color:'red'}}>
        <b>链改网空投每天领，今日领到</b>
      </div>
    );
    const content1 = (
      <div >
        奖励以好友领取值为基数的75%<br/>
        每日一领的奖励百分比将会依市场情况适时调整
      </div>
    );
    
  return (
    <div>
      <Card  type="inner" title="LG通证空投(LGC&ge;10)" extra={<Link to="/warning">More</Link>} >
            <div>新人福利：{this.state.AirdropAmount}
                <span style={{float:'right'}}>
                    <Button  onClick={() => {this.getAirdrop()}} 
                        disabled={this.state.chainIdDisabled} 
                        type="primary"  
                        size='small'>
                        领取
                    </Button>
                </span>
            </div>
            <div>
                {this.state.time}天1领：<span>余额{this.state.AirdropEveryDay_LGbalance}</span>
            </div>
            <div>
                <span>日额{this.state._adayamount}</span>
                
                <Button  
                        onClick={() => {this.addDayamount()}}
                        disabled={this.state.chainIdDisabled}   
                        size='small'>
                        供应
                    </Button>
                max{(web3.utils.fromWei(this.state.AirdropAmountEveryDay,"ether")*2*0.9).toFixed(2)}&nbsp;
            

                <span style={{float:'right'}}>

                    <Button onClick={() => {this.getAirdropEveryDay()}}
                        disabled={this.state.Disabled2}
                        type="primary" 
                        size='small'
                        >
                        领取
                    </Button>
                </span>   
            </div>

            <div style={{color:"green",fontSize:"12px"}}>
                  {this.state.message}
           </div>

           <div style={{marginTop:'5px'}} >
              <Popover content={content1} trigger="click">
                  <QuestionCircleOutlined /> 
              </Popover>
               邀请好友免费领8.88，奖励您6.66(天天有)：
              <div >
                <div style={{width:'18%',float:'left',marginRight:'2%'}}>
                    <Button type='primary' size="small" 
                      block
                      onClick = {this.friendInfoOpen}
                    >好友</Button>
                </div>
                <div style={{width:'80%',float:'right'}}>
                  <CopyToClipboard  text={this.state.inviteMy}>
                    <Button type="primary" size="small" 
                      block
                    >复制我的邀请链接</Button>
                  </CopyToClipboard>
                </div>
                <div style={{clear:'both'}}></div>
              </div>
           </div>

          {/* 加载窗  */}
          <Modal  visible={this.state.loadwindow} 
              onCancel={this.loadwindowClose} 
              footer={[
                <div style={{textAlign:'center'}} >
                  <Button type='primary' onClick={this.loadwindowClose}>关闭</Button>
                </div>,
                ]}
            >
              <div style={{textAlign:'center'}}>
                    <div style={{display:this.state.loading}}>
                      <Spin size="large" />
                      <div style={{color:"#1890ff",fontSize:"13px",textAlign:'center'}}>
                        {this.state.loadmessage}
                      </div>
                    </div>
                    <div style={{display:this.state.complete}}>
                      <Progress type="circle" percent={100} width={45}/>
                      <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                        {this.state.loadmessage}
                      </div>
                    </div>
              </div>
          </Modal>
          {/* 结果窗  */}
          <Modal title={content} visible={this.state.Resultwindow} 
              onCancel={this.ResultwindowClose} 
              footer={[
                <div style={{textAlign:'center'}} >
                  <Button type='primary' onClick={this.ResultwindowClose}>关闭</Button>
                </div>,
                ]}
            >
              <span>   
              <Result style={{padding:'30px'}}
                  icon={this.state._icon}
                  title={this.state.lastHB}
                />
              </span>
          </Modal>
          {/* 好友列表  */}
          <Modal title='' visible={this.state.friendWindow} 
              onCancel={this.friendWindowClose} 
              footer={[
                <div style={{textAlign:'center'}} >
                  <Button type='primary' onClick={this.friendWindowClose}>关闭</Button>
                </div>,
                ]}
            >
              <Table size='small' pagination={false} columns={columns}  
                style={{marginTop:'12px'}}
                dataSource={this.state.friendList} 
                pagination={{ pageSize: 10 }}
                customRow="click"
                onRow = {(record) => {
                  return {
                    onClick: () => {
                      console.log(record)
                      {this.setState({friendInfoWindow: true})};
                      {this.setState({friendInfo: record[0]})}
                      }
                    }
                    }
                    }
              />
              <Modal  visible={this.state.friendInfoWindow} 
                  onCancel={this.friendInfoClose} 
                  footer={[
                    <div style={{textAlign:'center'}} >
                      
                    </div>,
                    <div style={{textAlign:'center'}} >
                    <CopyToClipboard text={this.state.friendInfo}>
                      <Button type='primary' onClick={this.friendInfoClose}>
                        关闭
                      </Button>
                    </CopyToClipboard>
                  </div>,
                     ]}
                  >
                  <div style={{marginTop:'10px', width:'90%' }}>
                    {this.state.friendInfo}
                    <CopyToClipboard text={this.state.friendInfo}>
                      <Button><CopyOutlined/></Button>
                    </CopyToClipboard>
                  </div> 
              </Modal>
          </Modal>
      </Card>
    </div>
  );
}
}

export default lgairdrop;
